.web {
	min-height: 864px;
	background: url("../img/Rectangle_20.png") center (center / cover) no-repeat;
	.row {
		margin-top: 32px; }
	.mobile_item {
		position: relative;
		width: 320px;
		height: 280px;
		margin: 0 auto;
		margin-bottom: 19px;
		padding-top: 92px;
		&_1 {
			background: url('../img/photo1.png') center center/cover no-repeat; }
		&_2 {
			background: url('../img/photo2.png') center center/cover no-repeat; }
		&_3 {
			background: url('../img/photo3.png') center center/cover no-repeat; }
		&_4 {
			background: url('../img/photo4.png') center center/cover no-repeat; }
		&_5 {
			background: url('../img/photo5.png') center center/cover no-repeat; }
		&_6 {
			background: url('../img/photo6.png') center center/cover no-repeat; }
		&_subtitle {
			color: #ffffff;
			font-size: 24px;
			font-weight: 400;
			text-align: center; }
		&_plus {
			position: absolute;
			display: flex;
			justify-content: center;
			align-items: center;
			margin: 0 auto;
			bottom: 35px;
			left: 50%;
			transform: translateX(-50%);
			cursor: pointer;
			&::before {
				content: '';
				display: block;
				width: 14px;
				height: 2px;
				background-color: blue; }
			&::after {
				content: '';
				position: absolute;
				left: 50%;
				transform: translateX(-50%);
				height: 14px;
				width: 2px;
				background-color: blue; }
			&:hover {
				&::before, &::after {
					background-color: red;
					transition: 0.3s all; } } } } }


